/*---- Time line -----*/

.timeline__item:after {
	background: #fff !important;
}

.timeline__content {
	background-color: #fff;
	border-radius: 10px;
	display: block;
	padding: 1.25rem;
	position: relative;
}

.timeline {
	position: relative;
	margin: 0 0 2rem;
	padding: 0;
	list-style: none;
	top: 2px;

	&:before {
		background-color: #e9ecef;
		position: absolute;
		display: block;
		content: '';
		height: 100%;
		top: 0;
		bottom: 0;
		left: 4px;
	}
}

.timeline-item {
	position: relative;
	display: -ms-flexbox;
	display: flex;
	padding-left: 2rem;
	margin: .5rem 0;

	&:first-child:before,
	&:last-child:before {
		content: '';
		position: absolute;
		background: #fff;
		width: 1px;
		left: .25rem;
	}

	&:first-child {
		margin-top: 0;

		&:before {
			top: 0;
			height: .5rem;
		}
	}

	&:last-child {
		margin-bottom: 0;

		&:before {
			top: .5rem;
			bottom: 0;
		}
	}
}

.timeline-badge {
	position: absolute;
	display: block;
	width: 0.4375rem;
	height: 0.4375rem;
	left: 1px;
	top: .5rem;
	border-radius: 100%;
	border: 1px solid #fff;
	background: #adb5bd;
}

.timeline-time {
	white-space: nowrap;
	margin-left: auto;
	color: #9aa0ac;
	font-size: 87.5%;
}

.timeline__item--right .timeline__content:before {
	border-right: 12px solid rgba(238, 232, 239, 0.9);
}

.timeline__items img {
	border-radius: 7px;
}

/*----Timeline---*/

.cbp_tmtimeline {
	margin: 0;
	padding: 0;
	list-style: none;
	position: relative;

	&:before {
		content: '';
		position: absolute;
		top: 0;
		bottom: 0;
		width: 2px;
		background: #d7d8e8;
		left: 20%;
		margin-left: -6px;
	}

	>li {
		position: relative;

		&:first-child .cbp_tmtime span.large {
			font-size: 17px !important;
			font-weight: 700;
		}

		&:nth-child(odd) {
			.cbp_tmtime span:last-child {
				font-size: 13px;
			}

			.cbp_tmlabel {
				background: #fff;

				&:after {
					border-right-color: #ffffff;
				}
			}
		}

		.empty span {
			color: rgba(0, 0, 0, 0.5);
		}

		.cbp_tmtime {
			display: block;
			width: 21%;
			padding-right: 70px;
			position: absolute;

			span {
				display: block;
				text-align: right;

				&:first-child {
					font-size: 15px;
					font-weight: 700;
				}

				&:last-child {
					font-size: 14px;
				}
			}
		}

		.cbp_tmlabel {
			margin: 0 0 30px 25%;
			background: #fff;
			padding: 1.2em;
			position: relative;
			box-shadow: 0 0 2rem 0 rgba(136, 152, 170, 0.15) !important;

			&:after {
				right: 100%;
				border: solid transparent;
				content: " ";
				height: 0;
				width: 0;
				position: absolute;
				pointer-events: none;
				border-right-color: #ffffff;
				border-width: 10px;
				top: 10px;
			}

			blockquote {
				font-size: 16px;
			}

			.map-checkin {
				border: 5px solid rgba(235, 235, 235, 0.2);
				-moz-box-shadow: 0px 0px 0px 1px #ebebeb;
				-webkit-box-shadow: 0px 0px 0px 1px #ebebeb;
				box-shadow: 0px 0px 0px 1px #ebebeb;
				background: #3d3780 !important;
			}

			h2 {
				margin: 0px;
				padding: 0 0 5px 0;
				line-height: 26px;
				font-size: 18px;
				font-weight: 600;

				a {
					font-size: 15px;

					&:hover {
						text-decoration: none;
					}
				}

				span {
					font-size: 15px;
				}
			}

			p {
				margin-bottom: 0;
			}
		}

		.cbp_tmicon {
			width: 40px;
			height: 40px;
			speak: none;
			font-style: normal;
			font-weight: normal;
			font-variant: normal;
			text-transform: none;
			font-size: 1.4em;
			line-height: 40px;
			-webkit-font-smoothing: antialiased;
			position: absolute;
			color: #fff;
			background: #46a4da;
			border-radius: 50%;
			text-align: center;
			left: 20%;
			top: 0;
			margin: 0 0 0 -25px;
		}
	}

	.map {
		padding-top: 0 !important;
	}
}

@media screen and (max-width: 992px) and (min-width: 768px) {
	.cbp_tmtimeline {
		>li {
			.cbp_tmtime {
				padding-right: 60px;
				width: 25%;
			}

			.cbp_tmicon {
				left: 23%;
			}
		}

		&:before {
			left: 23%;
		}

		>li .cbp_tmlabel {
			margin: 0 0 15px 30%;
		}
	}
}

@media screen and (max-width: 65.375em) {
	.cbp_tmtimeline>li .cbp_tmtime span:last-child {
		font-size: 12px;
	}
}

@media screen and (max-width: 47.2em) {
	.cbp_tmtimeline {
		&:before {
			display: none;
		}

		>li {
			.cbp_tmtime {
				width: 100%;
				position: relative;
				padding: 0 0 20px 0;

				span {
					text-align: left;
				}
			}

			.cbp_tmlabel {
				margin: 0 0 30px 0 !important;
				padding: 1em;
				font-weight: 400;
				font-size: 95%;

				&:after {
					right: auto;
					left: 20px;
					border-right-color: transparent;
					border-bottom-color: #fff;
					top: -20px;
				}
			}

			.cbp_tmicon {
				position: relative;
				float: right;
				left: auto;
				margin: -64px 5px 0 0px;
			}

			&:nth-child(odd) .cbp_tmlabel:after {
				border-right-color: transparent;
				border-bottom-color: #fff;
			}
		}
	}
}