.navbar-light {
	.navbar-brand {
		color: rgba(0, 0, 0, 0.9);

		&:hover,
		&:focus {
			color: rgba(0, 0, 0, 0.9);
		}
	}

	.navbar-nav {
		.nav-link {
			color: rgba(0, 0, 0, 0.5);

			&:hover,
			&:focus {
				color: rgba(0, 0, 0, 0.7);
			}

			&.disabled {
				color: rgba(0, 0, 0, 0.3);
			}
		}

		.show>.nav-link,
		.active>.nav-link {
			color: rgba(0, 0, 0, 0.9);
		}

		.nav-link {

			&.show,
			&.active {
				color: rgba(0, 0, 0, 0.9);
			}
		}
	}

	.navbar-toggler {
		color: rgba(0, 0, 0, 0.5);
		border-color: rgba(0, 0, 0, 0.1);
	}

	.navbar-toggler-icon {
